export default {
	props: {
		tabBarList: Array,
		tabIndex: Number,
		// 自定义属性样式，默认为空不添加额外样式属性
		swiperTabStyle: {
			type: String,
			default: ''
		},
		// 自定义属性样式，默认为空不添加额外样式属性
		swiperTabItemStyle: {
			type: String,
			default: ''
		}
	},
	methods: {
		// 点击菜单
		tabTap (index) {
			this.$emit('tabTap', index)
		}
	}
}
